.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.image-upload-button {
  background-color: #bad4d5; /* A verdant hue */
  color: rgb(0, 0, 0);
  padding: 10px 24px;
  margin: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.image-upload-button:hover {
  background-color: #457672;
}

.OCR-button {
  background-color: #939696; /* A verdant hue */
  color: rgb(0, 0, 0);
  padding: 10px 24px;
  margin: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.OCR-button:hover {
  background-color: #5e7272;
}